<template>
	<div class="BgMusic">
		<button @click="musicControl" :class="{rotate:PlayButton}">
			<i class="fa" :class="PlayButton?'fa-pause':'fa-play'" aria-hidden="true"></i>
		</button>
		<audio :src="music" hidden="hidden"  loop="loop" preload="auto" autoplay="autoplay" class="media-audio" ref="MusicPlay"></audio>
	</div>
</template>

<script>
	export default {
		name:"BgMusic",
		data(){
			return{
				 music: require('../assets/audio/You Are Not Alone.mp3'),
				 PlayButton:true
			}
		},
		methods:{
			musicControl() {
			  //this.$refs.MusicPlay.pause();
				//alert(this.$refs.MusicPlay.src)
				if(this.$refs.MusicPlay.autoplay){
					this.$refs.MusicPlay.pause();
					this.$refs.MusicPlay.autoplay = false;
					this.PlayButton = false;
				}else{
					this.$refs.MusicPlay.play();
					this.$refs.MusicPlay.autoplay = true;
					this.PlayButton = true;
				}
			}
		}
	}
</script>

<style>
	
	.BgMusic{
		position: fixed;
		right: 6%;
		top: 73%;
		z-index: 999;
	}
	.BgMusic button{
		width: 120px;
		height: 120px;
		border-radius: 60px;
		border: none;
		/*
		border: 8px solid #333;
		*/
		background: url(../assets/img/singlecover.png) no-repeat center center,url(../assets/img/mke.jpg) no-repeat center center;
		background-size:cover,80px 80px;
		font-size: 22px;
		opacity: .98;
		color: white;
	}
</style>
